.welcome-hero {
  width: 40%;
  min-width: 40%;
  z-index: 100;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: flex-end;
}
.welcome-hero-container {
  display: flex;
  align-items: center;
  flex: 1;
  width: 90%;
  overflow: hidden;
  height: 70vh !important;
  min-height: 70vh;
}
.welcome-hero h1 {
  pointer-events: none;
  font-size: 5rem;
  color: #ffecb3;
  -webkit-text-stroke: 1px #ffecb3;
  text-align: start;
  width: 100%;
  margin-block-end: 0;
  font-weight: 1000;
  letter-spacing: 2px;
  width: 100%;
}
.welcome-hero span {
  color: #ffecb3;
}
.welcome-hero p {
  margin-bottom: 2rem;
  width: 100%;
  text-align: start;
  white-space: normal;
  font-size: 1.4rem;
  color: #ffecb3;
  font-weight: 200;
  padding: 1rem;
  pointer-events: none;
}
.welcome-hero-button {
  margin-top: 3rem;
  background: transparent;
  border: none;
  color: #ffecb3;
  font-weight: 100;
  outline: none;
  font-size: 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: 0.9;
  position: fixed;
  top: 88vh;
  display: none;
}
.welcome-hero-button h1 {
  margin-top: 1rem;
  font-size: 1rem !important;
  font-weight: 100 !important;
  -webkit-text-stroke: 0px;
}
.welcome-items {
  display: flex;
  align-items: center;
  flex: 1;
  z-index: 500;
  justify-content: flex-end;
}
.welcome-item {
  width: 250px;
  margin: 0.5rem;
  display: flex;
  align-items: center;
  flex-direction: column;
  background: #ffecb34f;
  box-shadow: 0px 0px 5px 0px #9c8e65;
}

.welcome-item h2 {
  font-weight: 600;
  font-size: 1.3rem;
  color: #ffecb3;
  letter-spacing: 5px;
  text-align: center;
  margin-block-start: 0em;
  height: 3rem;
  white-space: normal;
}
.welcome-item p1 {
  white-space: normal;
  font-size: 1.3rem;
  color: #ffecb3;
  display: none;
}
.welcome-item img {
  object-fit: cover;
  opacity: 1;
  box-shadow: 0px 0px 30px 0px black;
  width: 100%;
  height: 150px;
}
.welcome-item-text {
  padding: 1rem;
}
.welcome-item-text button {
  padding: 1rem 1.5rem;
  background: #ffecb3;
  font-weight: 100;
  font-size: 1.3rem;
  border: 0.5px solid #9c8e65;
  box-shadow: 0px 0px 10px 0px #ffecb3;
  border-radius: 5px;
  color: #443f30;
  margin-left: 0;
}
.welcome-footer {
  height: 15vh;
  width: 100%;
  text-align: center;
  font-size: 1.1rem;
  font-weight: 100;
  color: #ffecb3;
  background: #212024;
  box-shadow: 0px -5px 15px 0px #000000;
  display: flex;
  align-items: center;
  justify-content: center;
}
.welcome-footer h4 {
  font-weight: 100;
}
